<template>
<section>
  <!---->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
    <el-form-item prop="fenlei">
      <el-input v-model="filters.fenlei" placeholder="" />
    </el-form-item>
    <el-form-item prop="pinming">
      <el-input v-model="filters.pinming" placeholder=""/>
    </el-form-item>
    <el-form-item prop="caizhi">
      <el-input v-model="filters.caizhi" placeholder="" />
    </el-form-item>
    <el-form-item prop="guige">
      <el-input v-model="filters.guige" placeholder="" />
    </el-form-item>
    <el-form-item prop="gongsi">
      <el-input v-model="filters.gongsi" placeholder="" />
    </el-form-item>
    <el-form-item prop="changjia">
      <el-input v-model="filters.changjia" placeholder="" />
    </el-form-item>
    <el-form-item prop="date">
    <el-date-picker
    v-model="value1"
    type="datetimerange"
    range-separator=""
    start-placeholder=""
    end-placeholder="">
    </el-date-picker>
    </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAdd" icon="el-icon-plus"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAddList" icon="el-icon-plus"></el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="550" stripe border highlight-current-row v-loading="pageLoading">
  <el-table-column label="uid" width="80">
    <template slot-scope="scope">
    <span>{{ scope.row.uid }}</span>
    </template>
  </el-table-column>
  <el-table-column label="" width="100">
    <template slot-scope="scope">
    <span>{{ scope.row.date }}</span>
    </template>
  </el-table-column>
    <el-table-column label="" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="" width="100">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" width="100">
      <template slot-scope="scope">
      <span>{{ scope.row.changjia }}</span>
      </template>
    </el-table-column>
      <el-table-column label="" width="100" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="" width="100" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.txdsl }}</span>
         </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.fzjl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.cph }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.rkfhzl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.bangcha }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.huowei }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.hwsl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.shr }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.zhuangtai }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.beizhu }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.junzhi }}</span>
          </template>
        </el-table-column>
    <el-table-column label="" width="270">
    <template slot-scope="scope">
     <el-button
       size="mini"
       type="primary"
       @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>
     <el-button
       size="mini"
       type="danger"
       @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i></el-button>
    <el-button
         size="mini"
         type="primary"
         @click="handleEdit1(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>
   </template>
    </el-table-column>
  </el-table>

  <!---->
  <el-dialog :title="form && form.cangku ? '' : '' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="cangku">
        <el-input v-model="form.cangku" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.fenlei" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.changjia" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.pinming" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.caizhi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.guige" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.txdsl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.fzjl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.cph" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.huowei" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.gongsi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.shr" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false"></el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading"></el-button>
    </div>
  </el-dialog>

  <!---->
  <el-dialog :title="form1 && form1.cangku ? '' : '' " :visible.sync="form1Visible" :close-on-click-modal="false">
    <el-form :model="form1" label-width="60px" :rules="rules" ref="form1">
      <el-row>
      <el-col :span="5">
      <el-form-item label="" prop="cangku">
        <el-input v-model="form1.cangku" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form1.fenlei" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form1.changjia"disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.pinming" disabled="disabled"/>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form1.caizhi" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form1.guige" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form1.huowei" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="5">
      <el-form-item label="" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.cph" disabled="disabled"/>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="7">
      <el-form-item label="" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.gongsi" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.txdsl" disabled="disabled"/>
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="" prop="fenlei" label-width="80px">
        <el-input v-model="form1.fzjl" disabled="disabled"/>
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="7">
      <el-form-item label="" prop="fenlei" label-width="80px">
        <el-input v-model="form1.cph1" />
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="" prop="fenlei"  label-width="80px">
        <el-input v-model="form1.txdsl1" />
      </el-form-item>
      </el-col>
      <el-col :span="7">
      <el-form-item label="" prop="fenlei" label-width="80px">
        <el-input v-model="form1.fzjl1" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="form1Visible = false"></el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading"></el-button>
    </div>
  </el-dialog>
  <!---->
  <el-dialog :title="form2 ? '' : '' " :visible.sync="form2Visible" :close-on-click-modal="false" width="30%">
    <el-upload
      class="upload-demo"
      action="https://jsonplaceholder.typicode.com/posts/"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :before-remove="beforeRemove"
      multiple
      :limit="1"
      accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
      :on-exceed="handleExceed"
      :file-list="fileList">
      <el-button size="small" type="primary"></el-button>
      <div slot="tip" class="el-upload__tip">execl50M</div>
    </el-upload>
  </el-dialog>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '',trigger: 'blur'}],
  fenlei: [{required: true,message: '',trigger: 'blur'}]
}//rules
export default {
name: 'storeInput',
data(){
    return {
    //
    size: 20,
    //
    filters: {},
    //
    rows: [],
    //
    pageLoading: false,
    //
    form: {},
    form1: {},
    form2: {},
    //
    rules: rules,
    //
    formVisible: false,
    form1Visible: false,
    form2Visible: false,
    fileList: [],
    //
    formLoading: false
    }
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('/inStore/handleList', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  },
  handleEdit(index, row){
    this.form = Object.assign({}, row);
    this.formVisible = true;
   },
   handleEdit1(index, row){
     this.form1 = Object.assign({}, row);
     this.form1Visible = true;
    },
  handleEditSubmit(index, row){
  if (this.formLoading)
    return
  this.$refs.form.validate(valid => {
    if (!valid)
      return
    this.formLoading = true
    var params = {
    "uid":"this.form.uid"
    };
    api.post('/inStore/handleEdit', params).then((res) => {
      this.formLoading = false
      this.$message({
        showClose: true,
        message: res.data.data.message
      })
      })
    }).catch(e => this.formLoading = false)
  },
  handleDelete(index, row){
    this.form = Object.assign({}, row);
    this.$confirm(', ?', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
    }).then(() => {
    var params = {
    "uid":"this.form.uid"
    };
    this.pageLoading = true;
    api.post('/inStore/handleDelete', params).then((res) => {
      console.log(res);
      this.pageLoading = false;
      this.$message({
        type: 'success',
        message: res.data.data.message
      })
    })
    },() => {
    this.pageLoading = false;
    });
    },
  handleAdd(){
    this.form = {};
    this.formVisible = true;
   },
  handleAddList(){
  this.form2Visible = true;
    },
    handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      }
  }//methods
}//export default
</script>

<style scoped>
</style>



