<template>
<section>
  <!---->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
      <el-form-item>
        <el-input v-model="filters.query" placeholder="/" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="handleQuery" icon="el-icon-search"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" v-on:click="handleAdd" icon="el-icon-plus"></el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="500" stripe border highlight-current-row v-loading="pageLoading">

  <el-table-column label="" width="100">
    <template slot-scope="scope">
    <span style="margin-left: 10px">{{ scope.row.uid }}</span>
    </template>
  </el-table-column>
    <el-table-column label="" width="180" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span style="margin-left: 10px">{{ scope.row.name }}</span>
     </template>
    </el-table-column>
    <el-table-column prop="sex" label="" width="100" align="center" :show-overflow-tooltip="true">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.sex }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" width="180">
      <template slot-scope="scope">
      <span style="margin-left: 10px">{{ scope.row.dept }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" width="180">
      <template slot-scope="scope">
       <i class="el-icon-time"></i>
       <span style="margin-left: 10px">{{ scope.row.date }}</span>
     </template>
    </el-table-column>
    <el-table-column label="">
      <template slot-scope="scope">
       <el-button
         size="mini"
         type="primary"
         @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>
       <el-button
         size="mini"
         type="danger"
         @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i></el-button>
     </template>
    </el-table-column>
  </el-table>
  <!---->
  <el-col :span="24" class="toolbar">
    <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
    </el-pagination>
  </el-col>

  <!---->
  <el-dialog :title="form && form.id ? '' : '' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-form-item label="" prop="name">
        <el-input v-model="form.name" />
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-radio-group v-model="form.sex">
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false"></el-button>
      <el-button type="primary" @click.native="handleSubmit" :loading="formLoading"></el-button>
    </div>
  </el-dialog>

</section>
</template>

<script>
const rules = {
  name: [{
    required: true,
    message: '',
    trigger: 'blur'
  }],
  sex: [{
    required: true,
    message: '',
    trigger: 'change'
  }]
}

let data = () => {
  return {
    //
    page: 1,
    //
    size: 20,
    //
    total: 0,
    //
    filters: {},
    //
    rows: [],
    //
    pageLoading: false,
    //
    clientHeight: '100%',
    //
    form: {},
    //
    rules: rules,
    //
    formVisible: false,
    //
    formLoading: false
  }
}

let handleAdd = function() {
  this.form = {}
  this.form.sex = 1
  this.formVisible = true
}

let handleEdit = function(index, row) {
  this.form = Object.assign({}, row)
  this.formVisible = true
}

let handleDelete = function(index, row) {
  if (this.pageLoading)
    return

  this.$confirm(', ?', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
  }).then(() => {
    this.pageLoading = true
    this.$axios.get('/api/member/remove/' + row.id).then(res => {
      this.pageLoading = false
      if (!res.data.success) {
        this.$message({
          type: 'error',
          message: res.data.message
        })
        return
      }
      this.$message({
        type: 'success',
        message: '!'
      })
      this.page = 1
      this.getRows()
    }).catch(e => this.pageLoading = false)
  }).catch(e => {})
}

let getRows = function() {
  if (this.pageLoading)
    return
  this.pageLoading = true
  let params = {
    page: this.page,
    size: this.size,
    query: this.filters.query
  }
  //post
  api.get('/member/list', params).then((res) => {
    this.pageLoading = false;
    if (!res.data.member)
    return
    //
    this.total = 100;
    this.page++;
    //
    this.rows = res.data.member;
  })
}

let handleSubmit = function() {
  if (this.formLoading)
    return

  this.$refs.form.validate(valid => {
    if (!valid)
      return

    this.formLoading = true

    //http
    this.$axios.post('/api/member/save', this.form).then(res => {
      this.formLoading = false
      if (!res.data.success) {
        this.$message({
          showClose: true,
          message: res.data.message,
          type: 'error'
        });
        return
      }
      this.$message({
        type: 'success',
        message: '!'
      })

      //
      this.page = 1
      this.getRows()
      this.formVisible = false
    }).catch(e => this.formLoading = false)
  })
}

let handleQuery = function() {
  this.page = 1
  this.getRows()
}

let handleCurrentChange = function(val) {
  this.page = val
  this.getRows()
}

let initHeight = function() {
  this.clientHeight = (document.documentElement.clientHeight - 258) + 'px'
}

export default {
  data: data,
  methods: {
    //
    handleQuery,
    //
    handleAdd,
    //
    handleEdit,
    //
    handleDelete,
    //
    handleCurrentChange,
    //
    getRows,
    //
    initHeight,
    //
    handleSubmit
  },
  mounted: function() {
    window.addEventListener('resize', this.initHeight)
    this.initHeight()
    this.getRows()
  }
}
</script>

<style scoped>
</style>



