<template>
<section>
  <!---->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
    <el-form-item prop="query">
      <el-input v-model="filters.query" placeholder="" />
    </el-form-item>
    <el-form-item prop="query">
      <el-input v-model="filters.query" placeholder="" />
    </el-form-item>
      <el-form-item prop="fenlei">
        <el-input v-model="filters.fenlei" placeholder="" />
      </el-form-item>
      <el-form-item prop="pinming">
        <el-input v-model="filters.pinming" placeholder=""/>
      </el-form-item>
      <el-form-item prop="caizhi">
        <el-input v-model="filters.caizhi" placeholder="" />
      </el-form-item>
      <el-form-item prop="guige">
        <el-input v-model="filters.guige" placeholder="" />
      </el-form-item>
      <el-form-item prop="date">
      <el-date-picker
      v-model="value1"
      type="datetimerange"
      range-separator=""
      start-placeholder=""
      end-placeholder="">
      </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAdd" icon="el-icon-plus"></el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="550" stripe border highlight-current-row v-loading="pageLoading">
  <el-table-column label="" width="150">
    <template slot-scope="scope">
    <span>{{ scope.row.jhdh }}</span>
    </template>
  </el-table-column>
    <el-table-column label="" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="" width="100">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" width="100" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="" width="100" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.shuliang }}</span>
         </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.fzjl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.cph }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.beizhu }}</span>
          </template>
        </el-table-column>
    <el-table-column label="" width="180">
    <template slot-scope="scope">
     <el-button
       size="mini"
       type="primary"
       @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>
     <el-button
       size="mini"
       type="danger"
       @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i></el-button>
   </template>
    </el-table-column>
  </el-table>

  <!---->
  <el-dialog :title="form && form.cangku ? '' : '' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="jhdh">
        <el-input v-model="form.jhdh"/>
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="cangku">
        <el-input v-model="form.cangku" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.fenlei" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="pinming">
        <el-input v-model="form.pinming" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="caizhi">
        <el-input v-model="form.caizhi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="guige">
        <el-input v-model="form.guige" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="shuliang">
        <el-input v-model="form.shuliang" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fzjl">
        <el-input v-model="form.fzjl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="gongsi">
        <el-input v-model="form.gongsi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="cph">
        <el-input v-model="form.cph" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="beizhu">
        <el-input v-model="form.beizhu" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false"></el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading"></el-button>
    </div>
  </el-dialog>
  <!---->
  <el-dialog :title="form1 && form1.cangku ? '' : '' " :visible.sync="dialogTableVisible" width="80%" :close-on-click-modal="false">
  <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="70px" class="demo-dynamic">
  <el-row>
  <el-col :span="4">
  <el-form-item label="" prop="cangku">
    <el-input v-model="form1.cangku" />
  </el-form-item>
  </el-col>
  <el-col :span="4">
  <el-form-item label="" prop="gongsi">
    <el-input v-model="form1.gongsi" />
  </el-form-item>
  </el-col>
  <el-col :span="4">
  <el-form-item label="" prop="cph">
    <el-input v-model="form1.cph" />
  </el-form-item>
  </el-col>
  <el-col :span="4">
  <el-form-item label="" prop="beizhu">
    <el-input v-model="form1.beizhu" />
  </el-form-item>
  </el-col>
  </el-row>
  <el-form-item
    v-for="(domain, index) in dynamicValidateForm.domains"
    :label="''+index+'' "
    :key="domain.key"
    :prop="'domains.' + index + '.value'">
    <el-col :span="10">
    <el-select v-model="domain.value" placeholder="://///" style="width:400px;">
    <el-option
     v-for="item in options"
     :key="item.value"
     :label="item.label"
     :value="item.value">
     </el-option>
     </el-select>
    </el-col>
    <el-col :span="3">
    <el-input v-model="domain.shuliang" placeholder=""></el-input>
    </el-col>
    <el-col :span="3">
        <el-input v-model="domain.jianshu" placeholder=""></el-input>
    </el-col>
    <el-col :span="4">
        <el-button @click.prevent="removeDomain(domain)"></el-button>
    </el-col>
  </el-form-item>
 </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="addDomain"></el-button>
      <el-button @click.native="dialogTableVisible = false"></el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading"></el-button>
    </div>
  </el-dialog>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '',trigger: 'blur'}],
  fenlei: [{required: true,message: '',trigger: 'blur'}]
}//rules
export default {
name: 'storeInput',
data(){
    return {
    //
    size: 20,
    //
    filters: {},
    //
    rows: [],
    //
    pageLoading: false,
    //
    form: {},
    form1: {},
    //
    rules: rules,
    //
    formVisible: false,
    dialogTableVisible: false,
    //
    formLoading: false,
    options: [{
          value: '//HRB500E/20*9/998.925/190',
          label: '//HRB500E/20*9/888.925/190'
        }, {
          value: '//HRB500E/20*9/888.925/190',
          label: '//HRB500E/20*9/888.925/100'
        }, {
          value: '//Q235B/18(-3-5)/888.925/190',
          label: '//Q235B/18(-3-5)/888.925/100'
        }],
    value: '',
    dynamicValidateForm: {
          domains: [{
            value: '',
            shuliang: '',
            jianshu: '',
          }],
          leibie: '',
          shulang: '',
          jianshu: ''
        }
    }//return
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('/storeOrder/handleList', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  },
  handleEdit(index, row){
    this.form = Object.assign({}, row);
    this.formVisible = true;
   },
  handleEditSubmit(index, row){
  if (this.formLoading)
    return
  this.$refs.form.validate(valid => {
    if (!valid)
      return
    this.formLoading = true
    var params = {
    "uid":"this.form.uid"
    };
    api.post('/inStore/handleEdit', params).then((res) => {
      this.formLoading = false
      this.$message({
        showClose: true,
        message: res.data.data.message
      })
      })
    }).catch(e => this.formLoading = false)
  },
  handleDelete(index, row){
    this.form = Object.assign({}, row);
    this.$confirm(', ?', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
    }).then(() => {
    var params = {
    "uid":"this.form.uid"
    };
    this.pageLoading = true;
    api.post('/inStore/handleDelete', params).then((res) => {
      console.log(res);
      this.pageLoading = false;
      this.$message({
        type: 'success',
        message: res.data.data.message
      })
    })
    },() => {
    this.pageLoading = false;
    });
    },
  handleAdd(){
    this.form1 = {};
    this.dialogTableVisible = true;
   },
   addDomain() {
        this.dynamicValidateForm.domains.push({
          value: '',
          key: Date.now()
        });
      },
    removeDomain(item) {
        var index = this.dynamicValidateForm.domains.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.domains.splice(index, 1)
        }
      }
  }//methods
}//export default
</script>

<style scoped>
</style>



