<template>
<section>
  <!---->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="filters">
      <el-form-item prop="query">
        <el-input v-model="filters.query" placeholder="" />
      </el-form-item>
        <el-form-item prop="query">
        <el-input v-model="filters.query" placeholder="" />
      </el-form-item>
      <el-form-item prop="fenlei">
        <el-input v-model="filters.fenlei" placeholder="" />
      </el-form-item>
      <el-form-item prop="pinming">
        <el-input v-model="filters.pinming" placeholder=""/>
      </el-form-item>
      <el-form-item prop="caizhi">
        <el-input v-model="filters.caizhi" placeholder="" />
      </el-form-item>
      <el-form-item prop="guige">
        <el-input v-model="filters.guige" placeholder="" />
      </el-form-item>
      <el-form-item prop="date">
      <el-date-picker
      v-model="value1"
      type="datetimerange"
      range-separator=""
      start-placeholder=""
      end-placeholder="">
      </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleAdd" icon="el-icon-plus"></el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="550" stripe border highlight-current-row v-loading="pageLoading">
  <el-table-column label="" width="150">
    <template slot-scope="scope">
    <span>{{ scope.row.jhdh }}</span>
    </template>
  </el-table-column>
  <el-table-column label="" width="100">
    <template slot-scope="scope">
    <span>{{ scope.row.date }}</span>
    </template>
  </el-table-column>
    <el-table-column label="" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.changjia }}</span>
      </template>
    </el-table-column>
      <el-table-column label="" width="80" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="80">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="" width="80" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.zhongliang }}</span>
         </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.fzjl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.ganghao }}</span>
          </template>
        </el-table-column>
				<el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.huowei }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.hwsl }}</span>
          </template>
        </el-table-column>
				<el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.fahuoyuan }}</span>
          </template>
        </el-table-column>
				<el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.beizhu }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
          <span>{{ scope.row.ckch }}</span>
          </template>
        </el-table-column>
    <el-table-column label="" width="280">
    <template slot-scope="scope">
    <el-button
      size="mini"
      type="primary"
      @click="handlePrint(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>
     <el-button
       size="mini"
       type="primary"
       @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i></el-button>
     <el-button
       size="mini"
       type="danger"
       @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i></el-button>
   </template>
    </el-table-column>
  </el-table>

  <!---->
  <el-dialog :title="form && form.cangku ? '' : '' " :visible.sync="formVisible" :close-on-click-modal="false">
    <el-form :model="form" label-width="100px" :rules="rules" ref="form">
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="cangku">
        <el-input v-model="form.cangku" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.name" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.changjia" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.pinming" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.caizhi" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.guige" />
      </el-form-item>
      </el-col>
      </el-row>
      <el-row>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.txdsl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.fzjl" />
      </el-form-item>
      </el-col>
      <el-col :span="8">
      <el-form-item label="" prop="fenlei">
        <el-input v-model="form.cph" />
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click.native="formVisible = false"></el-button>
      <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading"></el-button>
    </div>
  </el-dialog>
  <el-dialog :title="form1 ? '' : '' " :visible.sync="form1Visible" width="60%" :close-on-click-modal="false">
  <el-form :model="dynamicValidateForm" label-width="100px" :rules="rules" ref="dynamicValidateForm">
  <el-form-item>
  <el-col :span="8">
       <el-input v-model="dynamicValidateForm.shuliang" placeholder=""/>
    </el-col>
    <el-col :span="4">
       <el-button type="primary" :loading="formLoading"></el-button>
    </el-col>
  </el-form-item>
  <el-form-item>
  <el-col :span="5">
    <el-input v-model="dynamicValidateForm.gongsi" placeholder=""/>
  </el-col>
  <el-col :span="5">
       <el-input v-model="dynamicValidateForm.cph" placeholder=""/>
    </el-col>
    <el-col :span="5">
      <el-input v-model="dynamicValidateForm.fhy" placeholder=""/>
    </el-col>
    <el-col :span="5">
      <el-input v-model="dynamicValidateForm.beizhu" placeholder=""/>
    </el-col>
  </el-form-item>
  <el-form-item
    v-for="(domain, index) in dynamicValidateForm.domains"
    :label="''+index+'' "
    :key="domain.key"
    :prop="'domains.' + index + '.value'">
    <el-col :span="12">
    <el-select v-model="domain.value" clearable  placeholder="://////" style="width:350px;">
    <el-option
     v-for="item in options"
     :key="item.value"
     :label="item.label"
     :value="item.value">
     </el-option>
     </el-select>
    </el-col>
    <el-col :span="4">
    <el-input v-model="domain.shuliang" placeholder="(1,2)"/>
    </el-col>
    <el-col :span="4">
        <el-input v-model="domain.jianshu" placeholder="(10,20)"></el-input>
    </el-col>
    <el-col :span="4">
        <el-button @click.prevent="removeDomain(domain)"></el-button>
    </el-col>
  </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="addDomain"></el-button>
    <el-button @click.native="form1Visible = false"></el-button>
    <el-button type="primary" @click.native="handleEditSubmit" :loading="formLoading"></el-button>
  </div>
  </el-dialog>

  <el-dialog :title="form2 ? '' : '' " :visible.sync="form2Visible" width="60%" :close-on-click-modal="false">
  <el-form :model="dynamicValidateForm" label-width="100px" :rules="rules" ref="dynamicValidateForm" id="printTest">
  <el-form-item>
  <el-col :span="4">
    <el-input v-model="form2.jhdh"/>
  </el-col>
  <el-col :span="4">
       <el-input v-model="form2.cangku" />
  </el-col>
  <el-col :span="4">
    <el-input v-model="form2.gongsi" />
  </el-col>
  <el-col :span="4">
       <el-input v-model="form2.ckch"/>
    </el-col>
    <el-col :span="4">
      <el-input v-model="form2.fahuoyuan"/>
    </el-col>
  </el-form-item>
  <el-form-item>
    <el-col :span="12">
     <el-input value="//Q235B/18(-3-5)//66.89/20" style="width:350px;"/>
    </el-col>
    <el-col :span="4">
    <el-input value="2"/>
    </el-col>
    <el-col :span="4">
        <el-input value="20"/>
    </el-col>
  </el-form-item>
  <el-form-item>
    <el-col :span="12">
     <el-input value="//HRB500E/20*9//888.925/190" style="width:350px;"/>
    </el-col>
    <el-col :span="4">
    <el-input value="1"/>
    </el-col>
    <el-col :span="4">
        <el-input value="10"/>
    </el-col>
  </el-form-item>
  <el-form-item>
    <el-col :span="12">
     <el-input value="//HRB400E/20*9//998.925/190" style="width:350px;"/>
    </el-col>
    <el-col :span="4">
    <el-input value="3"/>
    </el-col>
    <el-col :span="4">
      <el-input value="30"/>
    </el-col>
  </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click.native="form2Visible = false"></el-button>
    <el-button type="primary" v-print="'#printTest'"></el-button>
  </div>
  </el-dialog>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '',trigger: 'blur'}],
  fenlei: [{required: true,message: '',trigger: 'blur'}]
}//rules
export default {
name: 'storeOutput',
data(){
    return {
    //
    size: 20,
    //
    filters: {},
    //
    rows: [],
    //
    pageLoading: false,
    //
    form: {},
    form1: {},
    form2: {},
    //
    rules: rules,
    //
    formVisible: false,
    form1Visible: false,
    form2Visible: false,
    //
    formLoading: false,
    value: [],
    options: [{
        value: '//Q235B/18(-3-5)//66.89/20/10',
        label: '//Q235B/18(-3-5)//66.89/20/10'
      },{
       value: '//HRB500E/20*9//66.89/20/10',
       label: '//HRB500E/20*9//66.89/20/10'
       }],
       value: '',
       dynamicValidateForm: {
             domains: [{
               value: '',
               shuliang: '',
               jianshu: '',
             }],
             leibie: '',
             shulang: '',
             jianshu: ''
           }
    }//return
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('outStore/handleList', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.store;
     })
  },
  handleEdit(index, row){
    this.form = Object.assign({}, row);
    this.formVisible = true;
   },
  handleEditSubmit(index, row){
  if (this.formLoading)
    return
  this.$refs.form.validate(valid => {
    if (!valid)
      return
    this.formLoading = true
    var params = {
    "uid":"this.form.uid"
    };
    api.post('/store/handleEdit', params).then((res) => {
      this.formLoading = false
      this.$message({
        showClose: true,
        message: res.data.data.message
      })
      })
    }).catch(e => this.formLoading = false)
  },
  handleDelete(index, row){
    this.form = Object.assign({}, row);
    this.$confirm(', ?', '', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning'
    }).then(() => {
    var params = {
    "uid":"this.form.uid"
    };
    this.pageLoading = true;
    api.post('/store/handleDelete', params).then((res) => {
      console.log(res);
      this.pageLoading = false;
      this.$message({
        type: 'success',
        message: res.data.data.message
      })
    })
    },() => {
    this.pageLoading = false;
    });
    },
  handleAdd(){
    this.form1 = {};
    this.form1Visible = true;
   },
  handleAddList(){
     alert("");
    },
    addDomain() {
         this.dynamicValidateForm.domains.push({
           value: '',
           key: Date.now()
         });
       },
     removeDomain(item) {
         var index = this.dynamicValidateForm.domains.indexOf(item)
         if (index !== -1) {
           this.dynamicValidateForm.domains.splice(index, 1)
         }
       },
       handlePrint(index, row){
         this.form2 = Object.assign({}, row);
         this.form2Visible = true;
        },
  }//methods
}//export default
</script>

<style scoped>
</style>



