<template>
<section>
  <!---->
  <el-col :span="24" class="toolbar" style="padding-bottom: 0px;width: 100%;">
    <el-form :inline="true" :model="filters">
      <el-form-item prop="fenlei">
        <el-input v-model="filters.fenlei" placeholder="" />
      </el-form-item>
      <el-form-item prop="pinming">
        <el-input v-model="filters.pinming" placeholder=""/>
      </el-form-item>
      <el-form-item prop="caizhi">
        <el-input v-model="filters.caizhi" placeholder="" />
      </el-form-item>
      <el-form-item prop="guige">
        <el-input v-model="filters.guige" placeholder="" />
      </el-form-item>
      <el-form-item prop="gongsi">
        <el-input v-model="filters.gongsi"  placeholder="" />
      </el-form-item>
      <el-form-item prop="changjia">
        <el-input v-model="filters.changjia" placeholder="" />
      </el-form-item>
      <el-form-item prop="date">
      <el-date-picker
      v-model="value1"
      type="datetimerange"
      range-separator=""
      start-placeholder=""
      end-placeholder="">
      </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="handleList" icon="el-icon-search"></el-button>
      </el-form-item>
    </el-form>
  </el-col>
  <el-table :data="rows" style="width: 100%;overflow: auto;" :height="500" stripe border highlight-current-row v-loading="pageLoading">
    <el-table-column label="" width="100" :show-overflow-tooltip="true">
      <template slot-scope="scope">
      <span>{{ scope.row.cangku }}</span>
     </template>
    </el-table-column>
    <el-table-column label="" width="80">
      <template slot-scope="scope">
      <span>{{ scope.row.fenlei }}</span>
      </template>
    </el-table-column>
      <el-table-column label="" width="80" :show-overflow-tooltip="true">
        <template slot-scope="scope">
        <span>{{ scope.row.pinming }}</span>
       </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
        <span>{{ scope.row.caizhi }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" width="80">
        <template slot-scope="scope">
        <span>{{ scope.row.guige }}</span>
        </template>
      </el-table-column>
        <el-table-column label="" width="80" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.rksl }}</span>
         </template>
        </el-table-column>
        <el-table-column label="" width="80" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.rkjs }}</span>
         </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.jhdsl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80" :show-overflow-tooltip="true">
          <template slot-scope="scope">
          <span>{{ scope.row.rkjs }}</span>
         </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.cksl }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.ckjs }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.gongsi }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
          <span>{{ scope.row.changjia }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" width="300">
          <template slot-scope="scope">
          <span>{{ scope.row.date }}</span>
          </template>
        </el-table-column>
  </el-table>
</section>
</template>

<script>
const rules = {
  cangku: [{required: true,message: '',trigger: 'blur'}],
  fenlei: [{required: true,message: '',trigger: 'blur'}]
}//rules
export default {
name: 'storeManage',
data(){
    return {
    //
    size: 20,
    //
    filters: {},
    //
    rows: [],
    //
    pageLoading: false,
    //
    form: {},
    //
    rules: rules,
    //
    formVisible: false,
    //
    formLoading: false,
    value1: ''
    }
  },
  methods:{
  handleList() {
     var params = {
     "name":this.filters.query
     };
     this.pageLoading = true;
     api.get('/store/report', params).then((res) => {
       console.log(res);
       this.pageLoading = false;
       this.rows = res.data.report;
     })
  }
  }//methods
}//export default
</script>

<style scoped>
</style>



